.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modal-content {
    background-color: white;
    padding: 30px;
    border-radius: 8px;
    position: relative;
    max-width: 300px;
    width: 80%;
  }

  .modal-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .modal-input {
    height: 30px;
    width: 70%;
    max-width: 300px;
    border-radius: 4px;
  }
  
  .modal-button {
    height: 40px;
    width: 50%;
    max-width: 120px;
    border-radius: 6px;
    font-size: 18px;
  }

  .button-container {
    display: flex;
    gap: 10px;
    justify-content: flex-start;
  }